.footer {
  padding-top: 41px;
  padding-bottom: 42px;
  background-color: #030a1a;

  @include vp-767 {
    padding-top: 46px;
    padding-bottom: 52px;
  }
}

.footer__inner {
  display: grid;
  grid-template-columns: 95px 1fr auto;
  gap: 20px;

  @include vp-1199 {
    align-items: center;
  }

  @include vp-767 {
    display: block;
  }
}
