.group {
  display: grid;
}

.group--section {
  grid-template-columns: 50% 50%;

  @include vp-1199 {
    grid-template-columns: 1fr;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
  }
}

.group--contact {
  grid-template-columns: 64% 1fr;

  @include vp-1199 {
    grid-template-columns: 53.5% 1fr;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
  }
}

.group--contact .group__col:first-child {
  padding-right: 60px;
  margin-right: 60px;
  padding-left: 1px;
  border-right: 1px solid #74819c;

  @include vp-1199 {
    padding-right: 15px;
    margin-right: 0;
  }

  @include vp-767 {
    padding-right: 0;
    margin-right: 0;
    border-right: none;
    margin-bottom: 18px;
  }
}

.group--contact .group__col:last-child {
  @include vp-1199 {
    padding-left: 47px;
  }

  @include vp-767 {
    padding-left: 0;
  }
}

.group--form {
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  margin-bottom: 28px;

  @include vp-1199 {
    gap: 60px;
  }

  @include vp-767 {
    gap: 37px;
    grid-template-columns: 1fr;

    margin-bottom: 38px;
  }
}
